import React from "react";
import { Col, Container, Row } from "react-bootstrap";
import banner from "../../../assets/images/main_banner.svg";
import DetailedView from "./DetailedView/DetailedView";
import styles from "./Home.module.scss";
import UnusualHoly from "./UnusualHoly/UnusualHoly";
import Articles from "./Articles/Articles";
import galleryImg1 from "../../../assets/images/sample-gallery (1).jpg";
import galleryImg2 from "../../../assets/images/sample-gallery (2).jpg";
import galleryImg3 from "../../../assets/images/sample-gallery (3).jpg";
import { Link } from "react-router-dom";

const Home = () => {
	return (
		<>
			<Container>
				<div className={styles.banner_sec_img}>
					<img src={banner} alt="banner_img" />
				</div>
			</Container>
			<DetailedView />
			<UnusualHoly />
			<Articles />
			<section className={styles.gallery}>
				<Container>
				<h2>Gallery</h2>
					<Row>
						{
							[galleryImg1, galleryImg2, galleryImg3].map(item => (
								<Col md={4}>
									<Link to="" className={styles.gallery_box}>
										<img src={item} alt="" />
										<h3>Sewa Hazur Sahib</h3>
									</Link>
								</Col>
							))
						}
					</Row>
				</Container>
			</section>
		</>
	);
};

export default Home;
