import React from "react";
import logo_footer from "../../../assets/images/logo-johlan.png";
import styles from "./Footer.module.scss";
import { Container } from "react-bootstrap";
import { fbIcon } from "../../../assets/images/SvgIcons";

const Footer = () => {
  return (
    <>
      <section className={styles.footer_sec}>
        <Container>
          <div className={styles.footer_sec_inner}>
            <span>
              <img src={logo_footer} alt="logo_footer" />
            </span>
            <div className={styles.footer_sec_inner_details}>
              <h3>Anniversary Dates</h3>
              <ul>
                <li>Brahm Giani Sant Baba Harnam Singh Ji Maharaj</li>
                <li>Brahm Giani Sant Baba Basant Singh Ji Maharaj</li>
                <li>Brahm Giani Sant Baba Gian Singh Ji Maharaj</li>
                <li>
                  Brahm Giani Sant Baba Harbhajan Singh Ji Virakat Maharaj
                </li>
              </ul>
            </div>
            <div className={styles.footer_sec_inner_media}>
              <ul>
                <li>
                  <fbIcon />
                </li>
                <li>
                  <instaIcon />
                </li>
                <li>
                  <youtubeIcon />
                </li>
              </ul>
            </div>
          </div>
        </Container>
      </section>
    </>
  );
};

export default Footer;
